body {
  background-color: #5257b9;
}

.loader {
  width: 60vh;
  height: 60vh;
  background: linear-gradient(165deg, #fff 0%, #dcdcdc 40%, #aaa 98%, #0a0a0a 100%);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:before {
  content: "";
  width: 100%;
  height: 100%;
  filter: blur(3px);
  border-bottom: 0 solid rgba(255, 255, 255, .02);
  border-radius: 100%;
  animation: rotate 2s linear infinite;
  position: absolute;
  box-shadow: inset 0 -10px 20px 20px rgba(255, 255, 255, .25), inset 0 -5px 15px 10px rgba(255, 255, 255, .314), inset 0 -2px 5px rgba(255, 255, 255, .5), inset 0 -3px 2px rgba(255, 255, 255, .733), 0 2px #fff, 0 2px 3px #fff, 0 5px 5px rgba(255, 255, 255, .565), 0 10px 15px rgba(255, 255, 255, .376), 0 10px 20px 20px rgba(255, 255, 255, .25);
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

.breed-select {
  width: 200px;
  height: 40px;
  display: none;
}

.error {
  color: red;
  display: none;
}

